div {
  text-align: center;
  padding: 1rem;
}

h1 {
  font-size: max(3rem, 2vw);
  letter-spacing: 0.2rem;
  margin-bottom: 2rem;
}

input {
  padding: 1rem;
  margin: 0 auto 3rem;
  width: 30%;
  border-radius: 1rem;
  background: #333;
  border: none;
  outline: transparent;
  text-align: center;
  font-size: 1.2vw;
  letter-spacing: 0.1rem;
}

table {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  border: 2px solid rgb(0, 255, 0);
  border-radius: 1rem;
  padding: 3rem 1rem;
}

thead {
  font-weight: bold;
  font-size: max(2rem, 1.5vw);
  letter-spacing: 0.2rem;
}

tbody {
  font-size: max(1.5rem, 1vw);
}

td {
  padding: 0.5rem;
}

button {
  background-color: #333;
  outline: transparent;
  border: 2px solid transparent;
  padding: 0.5rem 2rem;
  border-radius: 1rem;
  font-size: 0.7vw;
  cursor: pointer;
  letter-spacing: 0.1rem;
  transition: all 0.5s ease;
}

button:hover {
  opacity: 0.7;
  border-color: rgba(0, 225, 0, 0.5);
}
